import { useContext, useState } from "react";
import "./styles/Registration.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { AuthContext } from "./context/AuthProvider";

const RegisterForm = (props) => {
  const { auth, setAuth } = useContext(AuthContext);

  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");
  const [repeatPassword, setRepeatPassword] = useState("");
  const [authStatus, setAuthStatus] = useState("");
  const [email, setEmail] = useState("");
  const navigate = useNavigate();

  if (auth?.user?.id) {
    navigate("/account");
  }

  function Register(event) {
    const setRegister = async () => {
      const response = await axios.post(props.api_url + "register", {
        username: login,
        email: email,
        password: password,
      });
      if (response.data == "user successfully created") {
        setAuthStatus(" ,    ");
        navigate("/login");
      } else if (response.data == "email already in use") {
        setAuthStatus("Email    ");
      } else if (response.data == "username already in use") {
        setAuthStatus(
          "     ,  "
        );
      } else {
        setAuthStatus("   ,  ");
      }
      setLogin("");
      setRepeatPassword("");
      setEmail("");
      setPassword("");
    };

    event.preventDefault();
    if (password != repeatPassword) {
      setAuthStatus("   !");
    } else {
      setRegister();
    }
  }

  return (
    <>
      <form className="register-form" onSubmit={Register}>
        <label> Email: </label>
        <input
          type="email"
          id="registerLoginInput"
          placeholder="  "
          onChange={(e) => {
            setEmail(e.target.value);
          }}
          value={email}
          required
        />
        <label> : </label>
        <input
          type="text"
          id="registerLoginInput"
          placeholder=" "
          onChange={(e) => {
            setLogin(e.target.value);
          }}
          value={login}
          required
        />
        <label> : </label>
        <input
          type="password"
          id="registePasswordInput"
          placeholder="  "
          onChange={(e) => {
            setPassword(e.target.value);
          }}
          value={password}
          required
        ></input>
        <label>  : </label>
        <input
          type="password"
          id="registePasswordInput"
          placeholder="  "
          onChange={(e) => {
            setRepeatPassword(e.target.value);
          }}
          value={repeatPassword}
          required
        ></input>
        <button></button>
        <div>{authStatus}</div>
      </form>
    </>
  );
};

export default RegisterForm;
